::selection{
    background-color: #9ec492;
}
.container{
    width: 100vw;
    /* height: 100vh; */
    display: flex;
    /* align-items: center; */
    justify-content: center;
    /* background-color: bisque; */
    /* margin-top: -1rem; */
}
.todo-box{
    width: 400px;
    background-color: #C4E8C2;
    height: auto;
    padding-bottom: 2rem;
    margin-top: 4rem;
    border-radius: 10px;
}
.box{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
}
.todo-box h4{
    font-size: 35px;
    font-weight: 900;
    font-family:monospace;
    /* color: #46A094; */
    color: #41436A;
    margin-bottom: 20px;
}
.todo-box input{
    width: 63%;
    height: 50px;
    padding-left: 20px;
    border-radius: 5px 0px 0px 5px;
    border: 2px solid #46A094;
    font-weight: 600;
    color: #2a2c45;
    
    /* background-color: transparent; */
}

.todo-box .add{
    width: 20%;
    height: 55.2px;
    border-radius: 0px 5px 5px 0px;
    border: 1px solid #46A094;
    background-color: #46A094;
    font-size: 15px;
    color: white;
    font-weight: 600;
    transition: 0.3s all ease-in;
}
.todo-box .add:hover{
    cursor: pointer;
    border: 1px solid #46A094;
    background-color: #2b7168;
}
.toDo-list{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    /* background-color: #46A094; */
}
.todo{
    display: flex;
    margin: 10px 0;
    justify-content: space-between;
    align-items: center;
    background-color: #AECFA4;
    width: 85%;
    height: auto;
    border-radius: 5px;
    padding: 0 10px;
}
.todo p{
    font-weight: 600;
    color: #2a2c45;
}
.todo button{
    height: 30px;
    width: 30px;
    border: 0px;
    background-color: #35867b;
    color: #C4E8C2;
    border-radius: 5px;
    transition: 0.3s all ease-in;

}

.todo button:hover{
    cursor: pointer;
    background-color: #2b7168;

}